.stepListItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: stretch;
        justify-content: stretch;
    -ms-flex-align: center;
        align-items: center;
    margin: 0.5em;
}

.stepListItem__name {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.stepListItem__moveUp, .stepListItem__moveDown, .stepListItem__delete {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: 2em;
    height: 1em;
    margin-left: 0.5em;
    cursor: pointer;
    text-align: center;
    -webkit-transition: color 0.25s;
    -o-transition: color 0.25s;
    transition: color 0.25s;
}

.stepListItem__delete {
    color: darkred;
}

.stepListItem__moveUp:hover, .stepListItem__moveDown:hover, .stepListItem__delete:hover {
    color: white;
}

.stepListItem__moveUp::before {
    content: '↑';
}

.stepListItem__moveDown::before {
    content: '↓';
}

.stepListItem__delete::before {
    content: 'x';
}


