.tile {
    background-color: #048;
    color: #fff;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    width: 1.25em;
    height: 1.25em;
    margin: 0.15em 0.075em;

    -webkit-transition: color 1s;

    -o-transition: color 1s;

    transition: color 1s;
}

.tile--blank {
    color: transparent;
}

.tile--long {
    font-size: 0.75em;
    
    /* scale these to be the same */
    width: 1.66667em;
    height: 1.66667em;
    margin: 0.2em 0.1em;
}

.tile:first-child {
    margin-left: 0.15em;
}

.tile:last-child {
    margin-right: 0.15em;
}


